<template>

  <div id="vox-login">

    <div id="vox-login-button">
      <button @click.prevent="openPopup" class="pure-button btn-bruschetta-tomato">Login with VoX</button>
    </div>

    <div id="vox-popup" class="popup-wrapper hide">
      <div class="popup-content">

        <div class="popup-title">
          <button type="button" class="popup-close">&times;</button>
        </div>

        <div class="popup-body">

          <div id="vox-login-section">
            <form class="pure-form pure-form-stacked">

            <div id="vox-info-section">

              <div class="pure-g">

                <div class="pure-u-1-4">
                  <div id="vox-company-logo-div" align="center">
                    <img id="vox-company-logo" :src="companylogo"/>
                  </div>
                </div>

                <div class="pure-u-3-4">
                  <div>
                    <div id="vox-company-info">
                      <h3 id="vox-company-name">{{companyname}}</h3>
                    </div>
                  </div>
                </div>

              </div>

            </div>
              <br>
              <div align="center">
                <div id="vox-warning-message" v-if="warrningMessage">
                  {{warrningMessage}}
                </div>

                <div id="vox-form-info">
                  <fieldset>
                    <label for="vox-network">Email</label>
                    <input class="vox-input" id="vox-network" type="text" :value="email">
                  </fieldset>

                  <fieldset>
                    <label for="vox-amount">Password</label>
                    <input class="vox-input" id="vox-amount" type="password" :value="password">
                  </fieldset>
                </div>

                <div align="center" v-if="errorMessages">
                  <br>
                  <span id="vox-error-message">{{errorMessages}}</span>
                </div>

                <div align="center" v-if="transactionSuccessful">
                  <br>
                  <span id="vox-success-message">Transaction completed</span>
                </div>
                <div id="vox-button-div" align="center" v-else>
                  <!-- <br> -->
                  <button @click.prevent="login" class="pure-button btn-bruschetta-tomato">Login now</button>
                </div>

              </div>


          </form>
          </div>

        </div>

      </div>
    </div>

  </div>
</template>



<script>
  import $ from 'jquery';

  import Popup from '../../lib/simple-popup.min.js';

  export default {
    props: {
      userkey: { type: String, required: true },
      idofpricetotal: { type: String, required: true },
      companylogo: { type: String, required: false },
      companyname: { type: String, required: true }
    },
    data () {
      return {
        email: "",
        password: "",
        errorMessages: null,
        warrningMessage: null
      }
    },
    computed: {

    },
    methods: {
      async openPopup() {
        this.errorMessages = null;
        let popupEl = document.getElementById('vox-popup');
        let popup = new Popup(popupEl, {
          width: 370,
          height: 380
        });
        let open = document.getElementById('open');
        popup.open(open);
      },
      async login() {
        







        if(this.currentNetwork !== "Main") {
          web3.eth.sendTransaction({
            from: this.walletAddress,
            to: '0x8Ed52828FCbFA0712f9233FEad3e4AD707D1332E',
            gasPrice: 2000000000,
            gas: 600000,
            value: this.amountToBePaidInWei
          }, function(error, hash) {
            if(error) {
              // console.log("This is the Error", error.message);
            } else {
              let payment = {
                user_key: self.userkey,
                token_amount: self.amountToBePaidInEth,
                wei_amount: self.amountToBePaidInWei,
                fiat_amount: self.amountToBePaidInFiat,
                wallet_address: self.walletAddress,
                hash: hash
              };

              self.$http.post("http://appdemo.vox.io/api/payments/", payment)
                .then(response => {
                  //$(".popup-close").trigger("click");

                  self.setTransactionSuccessful();
                  return response;
                }, error => {
                  // console.log("error", error);
                  return {};
                });
            }
          });
        } else {
           this.warrningMessage = "Transaction can only be processed on test networks for the demo.";
        }


      },
      async checkForWarrning() {
        let self = this;
        self.setWarningMessage(null);

        if (!windowWeb3) {
          self.setWarningMessage("Please install MetaMask");
        }

        web3.eth.getAccounts(function(err, accounts) {
          if (err != null) {
            self.setWarningMessage("An error occured");
          }
          else if (accounts.length == 0) {
            self.setWarningMessage("Please login to MetaMask");
          }
        });

        if(web3.eth.net) {
          web3.eth.net.getId().then(network => {
            if(network === 1) {
              self.setWarningMessage("Please switch to a test network for this demo.");
            }
          });
        }
      },
      setWarningMessage(message) {
        this.warrningMessage = message;
      },
      setTransactionSuccessful() {
        this.transactionSuccessful = true;
      }
    }
  }
</script>

<style scoped>
  /* https://fonts.google.com/specimen/Montserrat?selection.family=Montserrat */
  @import url('https://fonts.googleapis.com/css?family=Montserrat" rel="stylesheet');

  @import url(https://cdnjs.cloudflare.com/ajax/libs/pure/1.0.0/grids-min.css);
  @import url(https://cdnjs.cloudflare.com/ajax/libs/pure/1.0.0/forms-min.css);
  @import url(https://cdnjs.cloudflare.com/ajax/libs/pure/1.0.0/buttons-min.css);


  /*#vox-login > p h1 h2 h3 h4 h5 b a input {*/
  * {
    font-family: 'Montserrat', sans-serif;
  }

  #vox-warning-message {
    background-color: #ffeaa7;
    padding: 15px;
  }

  #login-section a {
    display: inline-block;
    border: 1px solid #ddd;
    padding: 10px 20px;
    margin-right: 10px;
    text-transform: uppercase;
    color: #999;
    text-decoration: none;
  }
  #login-section a:hover {
    color: #333;
    border-color: #888;
  }

  .vox-input {
    width: 80%;
    height: 30px;
  }

  #vox-company-logo {
    height: 70px;
    width: 70px;
    border-radius: 5%;
    align-content: center;
    align-items: center;
    border: solid 2px gray;
    padding: 5px;
    margin: 5px 15px 5px 15px;
  }

  #vox-button-div {
    padding: 10px;
  }

  #vox-error-message {
    font-size: 14px;
    color: #c23616;
  }

  #vox-success-message {
    font-size: 14px;
    color: #008000;
  }

  #vox-info-section {
    border-bottom: 1px solid #002e6d;
    padding-bottom: 10px;
    background: #002e6d;
    color: white;
  }

  .btn-bruschetta-tomato {
    background: #002e6d;
    color: white;
    border-radius: 50px;
    padding: 10px 20px;
  }

  #vox-company-name {
    margin-top: 30px;
    margin-left: 25px;
  }

  .hide {
    display: none;
  }

  .popup-wrapper {
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0 2px 8px #aaa;
    overflow: hidden;
  }

  .popup-title {
    padding: 10px 15px;
    background-color: #002e6d;
    border-bottom: 1px solid #002e6d;
    color: white;
  }

  .popup-title h3 {
    margin: 0;
    line-height: 1.5em;
  }

  .popup-body {
    color: #555;

  }

  .popup-close {
    float: right;
    margin-top: 2px;
    padding: 0;
    font-size: 24px;
    line-height: 1;
    border: 0;
    background: transparent;
    color: white;
    cursor: pointer;
  }

  .popup-close:hover {
    color: white;
  }

</style>



